___CSS_LOADER_EXPORT___.push([module.id, `.lds-heart {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  transform: rotate(45deg);
  transform-origin: 40px 40px;
}

.lds-heart div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: #fff;
  animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}

.lds-heart div:after,
.lds-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: #fff;
}

.lds-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}

.lds-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}

@keyframes lds-heart {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}`, "",{"version":3,"sources":["webpack://./src/shared/ui/Loader/Loader.scss"],"names":[],"mappings":"AAAA;EACI,qBAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,wBAAA;EACA,2BAAA;AACJ;;AACE;EACE,SAAA;EACA,UAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,gBAAA;EACA,sEAAA;AAEJ;;AAAE;;EAEE,YAAA;EACA,kBAAA;EACA,cAAA;EACA,WAAA;EACA,YAAA;EACA,gBAAA;AAGJ;;AADE;EACE,WAAA;EACA,0BAAA;AAIJ;;AAFE;EACE,UAAA;EACA,0BAAA;AAKJ;;AAHE;EACE;IACE,sBAAA;EAMJ;EAJE;IACE,qBAAA;EAMJ;EAJE;IACE,sBAAA;EAMJ;EAJE;IACE,mBAAA;EAMJ;EAJE;IACE,sBAAA;EAMJ;EAJE;IACE,qBAAA;EAMJ;AACF","sourcesContent":[".lds-heart {\r\n    display: inline-block;\r\n    position: relative;\r\n    width: 80px;\r\n    height: 80px;\r\n    transform: rotate(45deg);\r\n    transform-origin: 40px 40px;\r\n  }\r\n  .lds-heart div {\r\n    top: 32px;\r\n    left: 32px;\r\n    position: absolute;\r\n    width: 32px;\r\n    height: 32px;\r\n    background: #fff;\r\n    animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);\r\n  }\r\n  .lds-heart div:after,\r\n  .lds-heart div:before {\r\n    content: \" \";\r\n    position: absolute;\r\n    display: block;\r\n    width: 32px;\r\n    height: 32px;\r\n    background: #fff;\r\n  }\r\n  .lds-heart div:before {\r\n    left: -24px;\r\n    border-radius: 50% 0 0 50%;\r\n  }\r\n  .lds-heart div:after {\r\n    top: -24px;\r\n    border-radius: 50% 50% 0 0;\r\n  }\r\n  @keyframes lds-heart {\r\n    0% {\r\n      transform: scale(0.95);\r\n    }\r\n    5% {\r\n      transform: scale(1.1);\r\n    }\r\n    39% {\r\n      transform: scale(0.85);\r\n    }\r\n    45% {\r\n      transform: scale(1);\r\n    }\r\n    60% {\r\n      transform: scale(0.95);\r\n    }\r\n    100% {\r\n      transform: scale(0.9);\r\n    }\r\n  }\r\n  "],"sourceRoot":""}]);
